.modal-franchise__container {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 568px;
	height: 668px;
	background-color: #ffffff;

	border: 2px solid #f8d24e;
	border-radius: 10px;

	padding-top: 34px;
	padding-bottom: 34px;
	padding-left: 55px;
	padding-right: 55px;

}

.modal-name {
	text-align: center;
	font-family: 'Seymour One';
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 28px;
	margin-bottom: 24px;
	max-width: 332px;
	margin-left: auto;
	margin-right: auto;
}

.modal-description {
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 140%;
	color: #131313;
}


// ____________modal_form

.modal-form {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
}

.modal-form-form{
    display: block;
    width: 100%;
    height: 100%;
    padding-left: 40px;
    padding-right: 40px;
    margin-bottom: 32px;
}

.modal-form-field-input {
    width: 100%;
    height: 42px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    padding-left: 16px;
    color: #000000;

    &::placeholder {
        font-family: 'Montserrat';
        @include font(12px, 400, 17px);
        color: var(--second-font-color);
    }

    &:focus {
        outline: none;
        border: 1.5px solid #0A8817;
    
    }


}


.modal-form-field-input:focus ~ .modal-form-field-input-desc {
	top: -0.5px;
	color: #0a8817;
	font-size: 10px;
	padding: 0 10px;
	border-left: 1px solid #0a8817;
	border-right: 1px solid #0a8817;

	background: #ffffff;
}

.modal-form-field-comment {
	background: rgba(248, 210, 78, 0.1);
	border: 1px solid #84a178;
	border-radius: 10px;
}

.modal-form-field-input-desc {
	position: absolute;
	top: 50%;
	left: 15px;
	transform: translateY(-50%);

	text-transform: capitalize;
	pointer-events: none;

	font-family: "Montserrat";
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 140%;
	transition: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form-field {
	position: relative;
	display: block;
	margin-bottom: 16px;
}

.modal-form-comment {
	position: relative;
	display: block;
}

.modal-form-textarea {
	resize: none;
	width: 100%;
	height: 84px;
	padding: 10px 16px;
	background: rgba(248, 210, 78, 0.1);
	border: 1px solid #84a178;
	border-radius: 10px;
	padding-left: 16px;
	color: #000000;
	transition: border 250mc cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form-field-input-comment {
	position: absolute;
	top: 20%;
	left: 16px;
	transform: translateY(-20%);
	text-transform: capitalize;
	pointer-events: none;

	font-family: "Montserrat";
	font-style: normal;
	font-weight: 400;
	font-size: 14px;
	line-height: 140%;
	transition: 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form-textarea:focus {
	outline: none;
	border: 1.5px solid #0a8817;
}

.modal-form-textarea:focus ~ .modal-form-field-input-comment {
	top: -4px;
	color: #0a8817;
	font-size: 10px;
	padding: 0 10px;
	border-left: 1px solid #0a8817;
	border-right: 1px solid #0a8817;

	background: #ffffff;
}


.button-modal {
	align-self: center;

}

@media screen and (max-width: 767px) {
	.modal-franchise__container {
		padding-top: 28px;
		padding-bottom: 28px;
		padding-left: 28px;
		padding-right: 28px;

		max-width: 280px;
		height: 674px;
		
	
	}

	.modal-name {
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		align-items: center;
		text-align: center;
		margin-bottom: 24px;
	}
	
	.modal-description {
		font-weight: 400;
		font-size: 14px;
		line-height: 140%;
	}

	.modal-form-form {
		padding-left: 3px;
		padding-right: 3px;
		margin-bottom: 24px;
	}

	.modal-form {
		margin-top: 28px;
	}

	.modal-form-field {
		margin-bottom: 10px;
	}

	.modal-form-field-input {
		height: 32px;
	}

	.modal-form-textarea {
		height: 64px;

	}

	.modal-form-field-input-desc {
		font-weight: 400;
		font-size: 12px;
		line-height: 140%;
	}

	.modal-form-field-input-comment {
		font-weight: 400;
		font-size: 12px;
		line-height: 140%;

	}

	.modal-btn {
		padding: 12px 34px;
	}




}